import React, { useState } from 'react'

function InputTextComponent(props) {
    return (
        <input
            type="text"
            placeholder={props.placeholder}
            value={props.value}
            style={{
                width: '180px',
                padding: '2px',
                paddingLeft: '5px',
                fontSize: '18px'
            }} />
    )
}

export default InputTextComponent
