import React, { Component } from 'react';
import Calendar from "@ericz1803/react-google-calendar";
import { css } from "@emotion/react";

const API_KEY = "AIzaSyD3SkYIC-iwn2HgHJdJwQrwT9ZXUVOTeqk";
let calendars = [
    { calendarId: "areris981@gmail.com", color: "#B241D1" },
];

let styles = {
    //you can use object styles (no import required)
    calendar: {
        borderWidth: "3px", //make outer edge of calendar thicker
        backgroundColor: 'rgb(240,249,188)'
    },

    //you can also use emotion's string styles
    today: css`
   /* highlight today by making the text red and giving it a red border */
    color: red;
    border: 3px solid red;
    backgroundColor: green
  `
}

class ShowJadwal extends Component {
    render() {
        return (
            <div>
                <Calendar styles={styles} apiKey={API_KEY} calendars={calendars} />
            </div>
        )
    }
}

export default ShowJadwal